/* eslint-env node */
'use strict'; // Shimming starts here.

(function () {
  // Utils.
  var logging = require('./utils').log;

  var browserDetails = require('./utils').browserDetails; // Export to the adapter global object visible in the browser.


  module.exports.browserDetails = browserDetails;
  module.exports.extractVersion = require('./utils').extractVersion;
  module.exports.disableLog = require('./utils').disableLog; // Uncomment the line below if you want logging to occur, including logging
  // for the switch statement below. Can also be turned on in the browser via
  // adapter.disableLog(false), but then logging from the switch statement below
  // will not appear.
  // require('./utils').disableLog(false);
  // Browser shims.

  var chromeShim = require('./chrome/chrome_shim') || null;
  var edgeShim = require('./edge/edge_shim') || null;
  var firefoxShim = require('./firefox/firefox_shim') || null;
  var safariShim = require('./safari/safari_shim') || null; // Shim browser if found.

  switch (browserDetails.browser) {
    case 'opera': // fallthrough as it uses chrome shims

    case 'chrome':
      if (!chromeShim || !chromeShim.shimPeerConnection) {
        logging('Chrome shim is not included in this adapter release.');
        return;
      }

      logging('adapter.js shimming chrome.'); // Export to the adapter global object visible in the browser.

      module.exports.browserShim = chromeShim;
      chromeShim.shimGetUserMedia();
      chromeShim.shimMediaStream();
      chromeShim.shimSourceObject();
      chromeShim.shimPeerConnection();
      chromeShim.shimOnTrack();
      break;

    case 'firefox':
      if (!firefoxShim || !firefoxShim.shimPeerConnection) {
        logging('Firefox shim is not included in this adapter release.');
        return;
      }

      logging('adapter.js shimming firefox.'); // Export to the adapter global object visible in the browser.

      module.exports.browserShim = firefoxShim;
      firefoxShim.shimGetUserMedia();
      firefoxShim.shimSourceObject();
      firefoxShim.shimPeerConnection();
      firefoxShim.shimOnTrack();
      break;

    case 'edge':
      if (!edgeShim || !edgeShim.shimPeerConnection) {
        logging('MS edge shim is not included in this adapter release.');
        return;
      }

      logging('adapter.js shimming edge.'); // Export to the adapter global object visible in the browser.

      module.exports.browserShim = edgeShim;
      edgeShim.shimGetUserMedia();
      edgeShim.shimPeerConnection();
      break;

    case 'safari':
      if (!safariShim) {
        logging('Safari shim is not included in this adapter release.');
        return;
      }

      logging('adapter.js shimming safari.'); // Export to the adapter global object visible in the browser.

      module.exports.browserShim = safariShim;
      safariShim.shimGetUserMedia();
      break;

    default:
      logging('Unsupported browser!');
  }
})();